.island {
  position: relative;
  -webkit-box-shadow: 0px 10px 25px 5px #d3e0d8;
  -moz-box-shadow: 0px 10px 25px 5px #d3e0d8;
  box-shadow: 0px 10px 25px 5px #d3e0d8;
  -webkit-transition: box-shadow 0.3s ease-in-out, -webkit-box-shadow 0.3s ease-in-out;
}
-moz-transition box-shadow .3s ease-in-out,
-webkit-box-shadow .3s ease-in-out,
-o-transition box-shadow .3s ease-in-out {
  transition: box-shadow 0.3s ease-in-out, -webkit-box-shadow 0.3s ease-in-out;
  background-color: #d3e0d8;
}
.island figcaption {
  font-family: 'Catamaran', sans-serif;
  font-size: 64px;
  font-weight: 100;
  color: #495d79;
}
.island span {
  position: absolute;
  bottom: 45px;
  display: block;
  width: 390px;
  margin: 0 auto;
  left: 0;
  right: 0;
}
.island img {
  margin: 0 auto;
}
.main .island:nth-child(1) {
  -webkit-animation-delay: 0;
  -moz-animation-delay: 0;
  -o-animation-delay: 0;
  animation-delay: 0;
}
.main .island:nth-child(2) {
  -webkit-animation-delay: 200ms;
  -moz-animation-delay: 200ms;
  -o-animation-delay: 200ms;
  animation-delay: 200ms;
}
.main .island:nth-child(3) {
  -webkit-animation-delay: 300ms;
  -moz-animation-delay: 300ms;
  -o-animation-delay: 300ms;
  animation-delay: 300ms;
}
@media only screen and (max-width: 1390px) {
  .island span {
    width: 300px;
  }
  .island figcaption {
    font-size: 52px;
  }
}
@media only screen and (max-width: 1200px) {
  .island img {
    width: 280px;
  }
}
