.plane__list li:before {
  position: absolute;
  display: block;
  content: '';
  background: url("img/plus_01.png") no-repeat;
  background-position: 0 0;
  width: 35px;
  height: 35px;
}
@media only screen and (max-width: 1600px) {
  .plane__list li:before {
    position: absolute;
    display: block;
    content: '';
    background: url("img/plus_01.png") no-repeat;
    background-position: 0 0;
    width: 35px;
    height: 35px;
    left: -25px;
    top: -10px;
  }
}
@media only screen and (max-width: 992px) {
  .plane__list {
    display: inline-block;
  }
  .plane__list li:before {
    left: -45px;
    top: -5px;
  }
}
